import 'phaser';

export class SimpleScene extends Phaser.Scene {
  map = null;
  layer = null;

  preload() {
    this.load.tilemapTiledJSON('map', 'assets/tilemaps/scifimap.json');
    this.load.image('scifi_tilesheet', 'assets/tilemaps/scifi_tilesheet.png');
  }

  create() {
    this.add.text(100, 100, 'Hello Phaser!', { fill: '#0f0' });

    this.map = this.make.tilemap({ key: 'map' });
    // this.map.addTilesetImage('scifi_tilesheet', 'scifi_tilesheet');

    // this.layer = this.map.createLayer('background_layer');
    // this.layer = this.map.createLayer('walkable_layer');
    // this.layer = this.map.createLayer('resources_layer');
    // this.layer = this.map.createLayer('collidable_layer');

    this.layer.resizeWorld();
  }
}
